.App {
  text-align: center;
  background-color: #ebebeb;
  min-height: 100vh;
  color: #282c34;
}

.App-cover {
  height: 90vmin;
  width: 90vmin;
  border-radius: 50%;
  pointer-events: none;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* @media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
} */

.App-header {
  min-height: 10vh;
  display: grid;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
.App-content {
  min-height: 90vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  flex-grow: 1;
}
.App-link {
  color: #87bdc7;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
